
import Loading from 'vux/src/components/loading/index.vue'
import Msg from 'vux/src/components/msg/index.vue'
import Tabbar from 'vux/src/components/tabbar/tabbar.vue'
import TabbarItem from 'vux/src/components/tabbar/tabbar-item.vue'
import Group from 'vux/src/components/group/index.vue'
import Cell from 'vux/src/components/cell/index.vue'

import axios from 'axios';
import { extApiPrefix } from '../constants';

export default {
components: {
Loading, Msg, Tabbar, TabbarItem, Group, Cell
},
computed: {
loginLink:function () { // 根据平台决定跳转地址
if (this.$parent.isNative) {

return  "https://www.maxwon.cn/member";
} else {

return `/maxh5/login?return_uri=${window.location.href}`;
}
}
},
data() {
return {
loading: false,
showError: false,
errorMessage: "",
books: []  // book 列表
}
},
async created() {
await this.fetchBooks();
},
methods: {
/**
* 获取Book信息
*/
async fetchBooks() {
let self = this;
self.loading = true;
const url = `${extApiPrefix}/api/books`;
return axios.get(url).then(function (response) {
self.loading = false;
self.books = response.data;
})
.catch(function (error) {
self.loading = false;
const message = this.$parent.getErrorMsg(error);
self.$parent.showErrorMsg("fetchBooks error: " + message);
});
},
}
}



